<template>
  <div class="viewContainer">
    <v-container>
      <v-row>
        <v-col
          align-self="center"
          xs="12"
          offset-xs="0"
          sm="10"
          offset-sm="1"
          md="8"
          offset-md="2"
          lg="6"
          offset-lg="3"
          xl="4"
          offset-xl="4"
        >
          <v-container class="pa-0">
            <v-row dense>
              <v-col cols="4">
                <v-btn large rounded block color="primary" class="no-caps mb-3"><v-icon class="mr-1" size="16">mdi-file-pdf</v-icon> {{ $t('download_pdf') }}</v-btn>
              </v-col>
            </v-row>
          </v-container>

          <v-card elevation="14" :outlined="false">
            <v-hover>
              <template v-slot:default="{ hover }">
                <div>
                  <v-card elevation="0" tile color="transparent">
                    <v-row
                      align="end"
                      class="fill-height"
                    >
                      <v-col
                        align-self="start"
                        cols="12"
                      >
                        <v-avatar
                          class="ml-3"
                          color="grey"
                          size="128"
                        >
                          <v-img :src="$auth.user().avatar"></v-img>
                        </v-avatar>
                      </v-col>
                      <v-col class="py-0">
                        <v-list-item>
                          <v-list-item-content>
                            <v-list-item-title class="title" v-html="$auth.user().name"></v-list-item-title>
                            <v-list-item-subtitle v-html="$auth.user().bio"></v-list-item-subtitle>
                          </v-list-item-content>
                        </v-list-item>
                      </v-col>
                    </v-row>
                    <v-fade-transition>
                      <v-overlay
                        v-if="hover"
                        absolute
                        :style="{'margin-top' : ($auth.user().cover == null) ? '0' : '-184px'}"
                      >
                        <v-btn :to="{ name: 'user.profile' }" color="primary" rounded><v-icon class="mr-2" size="14">mdi-pencil</v-icon> {{ $t('edit_profile') }}</v-btn>
                      </v-overlay>
                    </v-fade-transition>
                  </v-card>
                </div>
              </template>
            </v-hover>

            <v-divider/>
            <v-card-actions class="pa-4">
              <v-btn color="primary" x-large rounded block :to="{name: 'user.experience'}"><v-icon class="mr-2">mdi-history</v-icon> {{ $t('experience') }}</v-btn>
            </v-card-actions>
            <v-divider/>

            <v-container v-if="resume !== null && Array.isArray(resume) && resume.length > 0">
              <v-row dense v-for="(link, i) in resume" :key="i">
                <v-col cols="1">
                  <div class="text-center d-flex align-start flex-column fill-height pa-1">
                    <div class="align-start flex-column">
                      <v-tooltip top>
                        <template v-slot:activator="{ on }">
                          <v-btn color="grey" x-small icon v-on="on" :disabled="link.first" @click="resumeAction('moveUp', link.uuid, resume[i-1].uuid)"><v-icon>mdi-arrow-up</v-icon></v-btn>
                        </template>
                        <span>{{ $t('move_up') }}</span>
                      </v-tooltip>
                    </div>
                    <div class="align-end flex-column mt-auto">
                      <v-tooltip bottom>
                        <template v-slot:activator="{ on }">
                          <v-btn color="grey" x-small icon v-on="on" :disabled="link.last" @click="resumeAction('moveDown', link.uuid, resume[i+1].uuid)"><v-icon>mdi-arrow-down</v-icon></v-btn>
                        </template>
                        <span>{{ $t('move_down') }}</span>
                      </v-tooltip>
                    </div>
                  </div>
                </v-col>
                <v-col cols="10">
                  <v-hover v-slot:default="{ hover }">
                    <v-card
                      :color="link.color"
                      :elevation="hover ? 12 : 2"
                      :href="link.url"
                      target="_blank"
                    >
                      <div class="d-flex flex-no-wrap justify-space-between">
                        <div>
                          <v-card-title class="headline" v-html="link.title"/>
                          <v-card-text style="margin-top:-16px" v-html="(link.description !== null) ? link.description.replace(/(?:\r\n|\r|\n)/g, '<br />') : null"/>
                        </div>
                      </div>
                      <v-fade-transition>
                        <v-overlay
                          v-if="hover"
                          absolute
                        >
                        </v-overlay>
                      </v-fade-transition>
                    </v-card>
                  </v-hover>
                </v-col>
                <v-col cols="1">
                  <div class="text-center d-flex align-start flex-column fill-height pa-1">
                    <div class="align-start flex-column">
                      <v-tooltip top>
                        <template v-slot:activator="{ on }">
                          <v-btn color="primary" x-small icon v-on="on" @click="resumeAction('edit', link.uuid, null, link)"><v-icon>mdi-pencil</v-icon></v-btn>
                        </template>
                        <span>{{ $t('edit_link') }}</span>
                      </v-tooltip>
                    </div>
                    <div class="align-end flex-column mt-auto">
                      <v-tooltip bottom>
                        <template v-slot:activator="{ on }">
                          <v-btn color="red" x-small icon v-on="on" @click="resumeAction('delete', link.uuid)"><v-icon>mdi-delete</v-icon></v-btn>
                        </template>
                        <span>{{ $t('delete_link') }}</span>
                      </v-tooltip>
                    </div>
                  </div>
                </v-col>
              </v-row>
            </v-container>

            <v-divider/>
            <v-card-actions class="pa-4">
              <v-btn color="primary" x-large rounded block :to="{name: 'user.projects'}"><v-icon class="mr-2">mdi-briefcase-outline</v-icon> {{ $t('projects') }}</v-btn>
            </v-card-actions>
            <v-divider/>

            <v-overlay
              v-if="resume === null || resumeLoading"
              absolute
            >
              <v-progress-circular indeterminate size="64"></v-progress-circular>
            </v-overlay>
          </v-card>
        </v-col>
      </v-row>
    </v-container>

  </div>
</template>
<script>
export default {
  data: () => ({
    locale: 'en',
    key: 0,
    resumeLoading: false,
    resume: null
  }),
  created () {
    // Show left drawer
    if (this.$vuetify.breakpoint.mdAndUp) this.$store.dispatch('setDashboardDrawer', true)

    // Set locale
    let locale = Intl.DateTimeFormat().resolvedOptions().locale || 'en'
    locale = (this.$auth.check()) ? this.$auth.user().locale : locale
    this.locale = locale

    this.moment.locale(this.locale.substr(0,2))

    this.getResume()
  },
  methods: {
    formatNumber (number) {
      return new Intl.NumberFormat(this.locale.replace('_', '-')).format(number)
    },
    resumeAction (action, uuid = null, uuid2 = null, link = null) {
      if (action == 'moveUp') {
        this.changeOrder(uuid, uuid2)
        return
      }

      if (action == 'moveDown') {
        this.changeOrder(uuid, uuid2)
        return
      }
    },
    getResume () {
      this.axios
        .get('/user/resume', { params: { locale: this.$i18n.locale }})
        .then(response => {
          this.resume = response.data
          this.resumeLoading = false
        })
      
    },
    changeOrder (uuid1, uuid2) {
      this.resumeLoading = true
      this.axios.post('/user/resume-projects/change-order', {
          locale: this.$i18n.locale,
          uuid1: uuid1,
          uuid2: uuid2
        })
      .then(res => {
        if (res.data.status === 'success') {
          this.getResume()
          this.$root.$snackbar(res.data.msg, {timeout: 2000})
        }
      })
      .catch(err => {
        if (err.response.data.status === 'error') {
          this.$root.$snackbar(err.response.data.msg, {timeout: 8000})
        }
        this.resumeLoading = false
      })
    }
  }
}
</script>